@use 'variables';

.event{
  padding: 80px 0;
  
  &--frontend{
    background-image: linear-gradient( variables.$blue1, variables.$blue2);
  }
  
  &--ui-ux{
    background-image: linear-gradient(variables.$blue2, variables.$blue1);
  }

  &--data-science{
    background-image: linear-gradient(variables.$blue1, variables.$blue2);
  }

  &--backend{
    background-image: linear-gradient(variables.$blue2, variables.$blue1);
  }

    &--image-left{
      .container{
        flex-direction: row-reverse;
      }
      
    }
  .container{
    display: flex;
    gap: 40px;
  }

  &__image{
    max-width: 360px;
  }

  &__detail{
    

    &__title{
      font-size: 18px;
      font-weight: bold;
      margin-bottom: 16px;
      margin-bottom: 16px;
      
    }
    
    &__description{
      
      line-height: 22px;
    }
  }
  @media (max-width: 640px){
    .container{
      display: block;
    }
    &__image{
      width: 100%;
    }
    &__detail{
      margin-right: 0px;
      margin-bottom: 16px;
    }

  }
}